<template>
  <div class="avatar" :style="styles">
    <img class="avatar__img" :src="src" :alt="alt" :style="{ borderRadius: radius }">
  </div>
</template>

<script>
export default {
  name: 'c-avatar',

  props: {
    src: {
      type: String,
      required: true
    },
    size: {
      type: [String, Number],
      default: 60,
      validator: v => Number(v) > 0
    },
    radius: {
      type: String,
      default: '0'
    },
    alt: {
      type: String,
      default: ''
    }
  },

  computed: {
    styles () {
      return {
        width: `${this.size}px`,
        height: `${this.size}px`,
        borderRadius: this.radius
      }
    }
  }
}
</script>

<style scoped>
  .avatar {
    position: relative;
    overflow: hidden;
  }
  .avatar__img {
    object-fit: cover;
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
  }
</style>